body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: #888;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: auto;
  padding: 0;
  height: 100%;
  width: 100%;
  max-width: 600px;

  display: flex;
  flex-direction: column;
  overflow: auto;
}

.container {
  margin: 4px;
  padding: 4px;
  width: calc(100% - 16px);
  flex: 1 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.container h2 {
  margin-bottom: 0px;
}

.header {
  margin: 4px;
  padding: 4px;
  width: calc(100% - 16px);
  border-bottom: 2px solid black;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.stat-text {
  margin: 4px;
}

.header-button {
  flex: 0 0 auto;

  position: relative;
  margin: 4px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;

  box-shadow: 0px 1px 4px 0px #222;
}

.header-button:active {
  background-color: #fff;
  box-shadow: 0px 0px 2px 0px #222;
}

.header-button.selected {
  background-color: #fff;
}

.bet-proba {
  padding: 8px;
  font-weight: 600;
  color: blue;
}

table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
}

table {
  margin-top: 16px;
}

th,
td {
  padding: 8px;
}

.make-bet-button {
  margin: 4px;
  padding: 8px;
  font-size: 100%;
  background-color: #eee;
  border-radius: 4px;
  box-shadow: 0px 1px 4px 0px #222;
}

.make-bet-button:active {
  background-color: #fff;
  box-shadow: 0px 0px 2px 0px #222;
}

.make-bet-button:hover {
  background-color: #fff;
}

.proba {
  position: relative;
  width: calc(100% - 56px);
  padding-top: calc(100% - 56px);
  margin: 4px;
  border-radius: 50%;
  box-shadow: 0px 0px 6px 0px #111;
}

.proba span {
  text-align: center;
  position: absolute;
  font-size: 24vw;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}

.confidence-control {
  font-size: 18vw;
}

.confidence-control-container {
  padding: 8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.filter-list {
  overflow-x: auto;
  display: flex;
  flex-direction: row;
}

.filter {
  flex: 0 1;
  padding: 8px;
  padding-left: 12px;
  padding-right: 12px;
  color: #111;
  background: #888;
  border-radius: 24px;
  margin-right: 8px;
  border: 2px solid black;
}

.filter.selected {
  color: #888;
  background: #111;
}

